body{
  background-color: #282c34;
}

.App {
  text-align: center;
  color: whitesmoke

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.box{
  margin-top: 20px;
  border: 1px solid black;
  margin-left: 35%;
  margin-right: 35%;
  border-radius: 10px;
  background-color: whitesmoke;
  color: black;

}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.btn{
  margin-top: 20px;
  
}

img{
  width: 70px;
  height: 60px;
  padding-right: 15px;
  padding-left: 2px;
  margin-bottom:-12px;
}

button{
  width:90px;
  height:30px;
  border-radius: 13px;
}


#addbox{
  border-radius:10px;
  border: 1px solid white;
}